body {
  background: #efe;
  color: #000;
  font-family: "游ゴシック体", "Yu Gothic", YuGothic, sans-serif;
  font-family: "游明朝体", "Yu Mincho", YuMincho, serif;
  font-family: 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
  font-family: "Hiragino Mincho ProN","HG明朝E","ＭＳ Ｐ明朝", serif;
  font-size: small;
  line-height: 1.4;
  word-wrap: break-word; }

html > body {
  height: auto;
  min-width: 980px; }

a:hover {
  text-decoration: none; }

#page {
  margin: 0 auto;
  width: 100%; }

.l-wrapper {
  height: 100%;
  position: relative;
  padding-bottom: 180px; }

/*
#####################################################################

.globalHeader
	
#####################################################################
*/
.l-globalHeader {
  margin: 0 auto;
  width: 980px;
  overflow: hidden;
  zoom: 1;
  position: relative; }
  .l-globalHeader--sitelogo {
    float: left;
    overflow: hidden;
    zoom: 1; }

/*
#####################################################################

.l-globalNav
	
#####################################################################
*/
.l-globalNav ul {
  overflow: hidden;
  padding: 0;
  background: #e6e600; }

.l-globalNav__btn {
  display: inline-block;
  padding: 10px 0; }
  .l-globalNav__btn a {
    margin: 0;
    padding: 10px 20px;
    background: #00e6e6; }

.serch-wrap {
  position: absolute;
  top: 14px;
  right: 0; }

/*
#####################################################################

.l-main
	
#####################################################################
*/
.l-main {
  width: 980px;
  margin: 0 auto; }

/*
#####################################################################

.l-bread
	
#####################################################################
*/
.l-bread {
  margin: 0;
  padding: 0; }
  .l-bread__item {
    display: inline-block; }
    .l-bread__item:after {
      content: ">";
      padding: 0 10px; }
    .l-bread__item:last-child:after {
      display: none; }

/*
#####################################################################

.globalFooter
	
#####################################################################
*/
.l-globalFooter {
  width: 100%;
  height: 180px;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #e6e600; }
  .l-globalFooter__inner {
    margin: 0 auto;
    width: 980px;
    overflow: hidden;
    position: relative; }

.l-footerNav {
  width: 100px;
  margin-bottom: 30px;
  float: left; }
  .l-footerNav__block {
    margin: 0;
    padding: 0 0 0 20px; }

.l-footerSNSIcon {
  clear: both;
  position: absolute;
  top: 10px;
  right: 0; }
  .l-footerSNSIcon__link {
    color: #000; }

.copylight {
  text-align: center;
  background: #eee;
  padding: 10px 0; }

/*
#####################################################################

.contents
	
#####################################################################
*/
.contents {
  position: relative;
  width: 980px;
  margin: 0 auto;
  background: #fff;
  min-height: 500px; }

.contents .pageTop {
  clear: both;
  text-align: right; }

.contents .pageTop a {
  padding: 0 0 0 18px; }

/* Mozilla based browsers */
::-moz-selection {
  background-color: #000;
  color: #fff; }

/* Works in Safari */
::selection {
  background-color: #000;
  color: #fff; }

